*{
  margin: 0;
  padding: 0;
}

/* Centering all the elements */
body{
  background-color: gray;
  display: grid;
  justify-content: center;
  align-items: center;
}

/* Styles for Question box in Home Page and the Mode Choice Table */
.question-box,.Mode-Choice-Table{
  margin: 5vh;
  padding: 5vh 5vw;
  background-color: white;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  gap: 2vh;
}
.form{
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.question{
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}
.options{
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.option{
  display: flex;
  gap: 0.5rem;
}

/* Styles for the inside elements of the Mode Choice Table */
.table {
  border: 2px solid black;
  text-align: center;
  width:100%;
}
td{
  border: 1px solid black;
  padding: 5px;
}
td:nth-child(odd){
  background-color: white;
}
td:nth-child(even){
  background-color: darkgray;
}
#joint{
  background-color: gray;
  color: white;
}
.icon{
  margin-top: 10px;
}